
<div class="s-selector">
  <multiselect
    v-bind="multiselectProps"
    :style="styleObject"
    :options="options"
    :max-height="200"
    v-on="$listeners"
  >
    <template #selection="{ option, values, isOpen }">
      <slot
        name="selection"
        :option="option"
        :values="values"
        :isOpen="isOpen"
      />
    </template>
    <template #singleLabel="{ option }">
      <slot name="singleLabel" :option="option"></slot>
    </template>
    <template #option="{ option }">
      <slot name="option" :option="option"></slot>
    </template>
    <template #beforeList>
      <slot name="beforeList" />
    </template>
    <template #afterList>
      <slot name="afterList" />
    </template>
    <template #noResult>
      <slot name="noResult" />
    </template>
    <template #placeholder>
      <slot name="placeholder" />
    </template>
  </multiselect>
</div>
